<template>
    <div class="index-layout" v-cloak>
        <IndexHeaderView></IndexHeaderView>
        <IndexMainView></IndexMainView>
    </div>
</template>
<script>
  import {getSignInfo} from '../../api/tiezidetail';
  import timeUtil from '../../utils/timeutil'
  import ftime from '../../utils/ftime'
  import wx from 'weixin-js-sdk';
  import Api from '../../api/api';
    export default {
        name: 'app',
        data () {
            return {
               msg:'index',
            }
        },
        components:{
            IndexHeaderView:function (resolve) {
              require(['./IndexHeader.vue'], resolve)
            },
            IndexMainView:function (resolve) {
              require(['./IndexMain.vue'], resolve)
            },
        },
      mounted(){
          if(this.isWeiXin()){
              this.getAsign();
          }
      },
      methods:{
        /**
         * 
         * */
        isWeiXin(){
          //
          let ua = window.navigator.userAgent.toLowerCase();
          if (ua.match(/MicroMessenger/i) == 'micromessenger') {
            return true;
          } else {
            return false;
          }
        },
        getAsign(){
          const _this = this;
          //url()
          let url = encodeURIComponent(window.location.href.split('#')[0]);
          //
          getSignInfo(Api.SIGN,url).then((res)=>{
            _this.initAsign(res);
          },(err)=>{
          });
        },
        /**
         * 
         * */
        initAsign(res){
          const _this = this;
          if(res.status === 200){
            //,
            let sinObj = res.data.data.detail;
            wx.config({
              debug: false,
              appId: sinObj.appId, // Ticke------
              timestamp:sinObj.timestamp.toString(), // 
              nonceStr: sinObj.nonceStr, // 
              signature: sinObj.signature,// 1
              //:QQQQ
              jsApiList: [
                'onMenuShareAppMessage','onMenuShareTimeline',
                'onMenuShareQQ','onMenuShareQZone'
              ]
            });
          }
        },
      }
    }
</script>
<style scoped>
    @import "../../assets/css/index.css";
</style>



